.explore-button {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  width: 150px;
  height: 150px;
  padding: 0;
  margin-inline: auto;
  border-radius: 50%;
  border: none;
  font-family: var(--ff-heading);
  font-size: 20px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  color: var(--dark-blue);
}

.explore-button:focus-visible {
  outline: none;
}

.explore-button::before {
  content: '';
  position: absolute;
  z-index: -1;
  width: 150px;
  height: 150px;
  background: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-radius: 50%;
  transition: all 80ms linear;
}

.explore-button:hover::before,
.explore-button:focus-visible::before {
  width: 220px;
  height: 220px;
  outline: none;
}

.explore-button__text {
  margin-top: 10px;
}

