    --navbar-height: 64px;
}
.menu__wrapper {
    display: flex;
    position: fixed;
    justify-content: space-evenly;
    width: 100vw;
	min-width: 350px;
    z-index: 3;
	right: 0;
    grid-gap: 24px;
    gap: 24px;
	background: rgba(255, 255, 255, 0.15);
	-webkit-backdrop-filter: blur(10px);
	        backdrop-filter: blur(10px);
    height: var(--navbar-height);
    padding: 0px 16px;
    align-items: center;
	border-radius: 10px 0 0 10px;
}

.menu__bar {
    display: flex;
    align-items: center;
	justify-content: center;
}

.logo {
    cursor: pointer;
    display: flex;
	max-width: 20%;
    align-items: center;
}

.logo svg {
    fill: var(--primary);
}

.navigation {
    margin-left: 32px;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
}

.navigation>li {
    display: flex;
    position: relative;
    cursor: pointer;
    align-items: center;
    min-height: 36px;
}

.navigation>li>a {
    color: var(--link-gray);
    transition: all 0.3s ease;
    text-decoration: none;
    border: none;
    cursor: pointer;
    z-index: 1;
    margin-right: -4px;
    align-items: center;
    min-height: 36px;
    font-size: 14px;
    grid-gap: 4px;
    gap: 4px;
    font-family: 'Open Sans', sans-serif;
    flex-wrap: nowrap;
    border-radius: 999px;
    padding: 4px 10px;
    white-space: nowrap;
    background: none;
    display: flex;
    position: relative;
    transition: all 0.2s ease-in-out;
}

.navigation>li>a:hover {
    color: var(--link-gray-hover);
    background-color: var(--gray);
}

.navigation>li>button {
    color: var(--link-gray);
    transition: all 0.3s ease;
    text-decoration: none;
    border: none;
    cursor: pointer;
    z-index: 1;
    margin-right: -4px;
    align-items: center;
    min-height: 36px;
    font-size: 14px;
    grid-gap: 4px;
